<template>
  <div class="itemBox">
    <p class="title">标题文本</p>
    <el-form ref="form" :rules="rules" :model="itemData" size="mini">
      <el-form-item label="标题名称" prop="title">
        <el-input v-model.trim="itemData.title" maxlength="10" placeholder="最多输入10个字"></el-input>
      </el-form-item>
      <el-form-item label="字体大小">
        <el-select v-model="itemData.fontSize" placeholder="请选择">
          <el-option :label="20" :value="20"></el-option>
          <el-option :label="24" :value="24"></el-option>
          <el-option :label="28" :value="28"></el-option>
          <el-option :label="32" :value="32"></el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="文本样式" class="navigation">
        <el-radio-group v-model="itemData.textSelect" @change="selectStyle">
          <el-radio :label="0">常规</el-radio>
          <el-radio :label="1">加粗</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="显示位置" class="navigation">
        <el-radio-group v-model="itemData.showAddress" @change="selectAlign">
          <el-radio :label="0">居左</el-radio>
          <el-radio :label="1">居中</el-radio>
          <el-radio :label="2">居右</el-radio>
        </el-radio-group>
      </el-form-item>

      <el-form-item label="文字颜色" class="showType bottomLine">
        <div class="sendType">
          <span class="showClass">推荐颜色</span>
          <div class="selctColor">
            <span
              class="bg1"
              @click="textColorChange(0)"
              :class="itemData.textColorSelect==0?'selsecColor':''"
            >
              <span></span>
            </span>
            <span
              class="bg2"
              @click="textColorChange(1)"
              :class="itemData.textColorSelect==1?'selsecColor':''"
            >
              <span></span>
            </span>
            <span
              class="bg3"
              @click="textColorChange(2)"
              :class="itemData.textColorSelect==2?'selsecColor':''"
            >
              <span></span>
            </span>
            <span
              class="bg4"
              @click="textColorChange(3)"
              :class="itemData.textColorSelect==3?'selsecColor':''"
            >
              <span></span>
            </span>
            <span
              class="bg5"
              @click="textColorChange(4)"
              :class="itemData.textColorSelect==4?'selsecColor':''"
            >
              <span></span>
            </span>
            <span
              class="bg6"
              @click="textColorChange(5)"
              :class="itemData.textColorSelect==5?'selsecColor':''"
            >
              <span></span>
            </span>
          </div>
        </div>
        <div class="sendType">
          <span class="showClass">自定义颜色</span>
          <div class="selctColor">
            <el-color-picker v-model="itemData.textColor" size="mini" @change="activeChange(1)"></el-color-picker>
          </div>
        </div>
      </el-form-item>
      <el-form-item label="背景颜色" class="showType">
        <div class="sendType">
          <span class="showClass">推荐颜色</span>
          <div class="selctColor">
            <span class="bg7" @click="bgChange(0)" :class="itemData.bgSelect==0?'selsecColor':''">
              <span></span>
            </span>
            <span class="bg8" @click="bgChange(1)" :class="itemData.bgSelect==1?'selsecColor':''">
              <span></span>
            </span>
            <span class="bg9" @click="bgChange(2)" :class="itemData.bgSelect==2?'selsecColor':''">
              <span></span>
            </span>
            <span class="bg10" @click="bgChange(3)" :class="itemData.bgSelect==3?'selsecColor':''">
              <span></span>
            </span>
            <span class="bg11" @click="bgChange(4)" :class="itemData.bgSelect==4?'selsecColor':''">
              <span></span>
            </span>
            <span class="bg12" @click="bgChange(5)" :class="itemData.bgSelect==5?'selsecColor':''">
              <span></span>
            </span>
          </div>
        </div>
        <div class="sendType">
          <span class="showClass">自定义颜色</span>
          <div class="selctColor">
            <el-color-picker
              v-model="itemData.backgroundColor"
              size="mini"
              @change="activeChange(2)"
            ></el-color-picker>
          </div>
        </div>
      </el-form-item>
    </el-form>
    <!-- {{itemData}} -->
  </div>
</template>

<script>
export default {
  props: {
    //父级传递的数据
    itemData: {
      type: Object
    }
  },
  data() {
    return {
      rules: {
        title: [{ required: true, message: "请输入标题名称", trigger: "blur" }]
      },
      predefineColors: [
        "#ff4500",
        "#ff8c00",
        "#ffd700",
        "#90ee90",
        "#00ced1",
        "#1e90ff",
        "#c71585"
      ]
    };
  },
  methods: {
    //文本样式变化
    selectStyle() {
      if (this.itemData.textSelect == 0) {
        this.itemData.fontWeight = 100;
      } else {
        this.itemData.fontWeight = 700;
      }
    },
    //显示位置变化
    selectAlign() {
      if (this.itemData.showAddress == 0) {
        this.itemData.showAlign = "left";
      } else if (this.itemData.showAddress == 1) {
        this.itemData.showAlign = "center";
      } else {
        this.itemData.showAlign = "right";
      }
    },
    //文字颜色变化
    textColorChange(e) {
      // console.log(e);
      this.itemData.textColorSelect = e;
      switch (e) {
        case 0:
          this.itemData.textColor = "#3a3a3a";
          break;
        case 1:
          this.itemData.textColor = "#db2828";
          break;
        case 2:
          this.itemData.textColor = "#fb5d06";
          break;
        case 3:
          this.itemData.textColor = "#c29a6c";
          break;
        case 4:
          this.itemData.textColor = "#558bd3";
          break;
        case 5:
          this.itemData.textColor = "#ffffff";
          break;
      }
    },
    //背景颜色变化
    bgChange(e) {
      this.itemData.bgSelect = e;
      switch (e) {
        case 0:
          this.itemData.backgroundColor = "#ffffff";
          break;
        case 1:
          this.itemData.backgroundColor = "#f44877";
          break;
        case 2:
          this.itemData.backgroundColor = "#ffecee";
          break;
        case 3:
          this.itemData.backgroundColor = "#fbf5dd";
          break;
        case 4:
          this.itemData.backgroundColor = "#deebf2";
          break;
        case 5:
          this.itemData.backgroundColor = "#333";
          break;
      }
    },

    activeChange(e) {
      // console.log(e)
      // console.log(this.itemData.textColor)
      // console.log(this.itemData.backgroundColor)
      if (e == 1) {
        if (
          this.itemData.textColor != "#3a3a3a" ||
          "#db2828" ||
          "#fb5d06" ||
          "#c29a6c" ||
          "#558bd3" ||
          "#ffffff"
        ) {
          this.itemData.textColorSelect = 6;
        }
      }else{
        if (
          this.itemData.backgroundColor != "#ffffff" ||
          "#f44877" ||
          "#ffecee" ||
          "#fbf5dd" ||
          "#deebf2" ||
          "#333"
        ) {
          this.itemData.bgSelect = 6;
        }
      }
    }
  }
};
</script>

<style scoped lang="less">
.active {
  border: 1px solid #000;
}
.title {
  border-bottom: 1px solid #eee;
  padding: 20px 18px 24px 18px;
  color: #333;
  font-size: 16px;
  font-weight: 700;
}
.itemBox /deep/ .el-form {
  margin: 18px 24px;
  .el-form-item__label {
    display: block;
    float: inherit;
    text-align: inherit;
    padding: 0;
    color: #222;
    font-weight: 100;
    margin-bottom: 14px;
  }
  .el-select {
    width: 100%;
  }
  .el-input__inner {
    height: 34px;
    border-radius: 2px;
  }
  .el-form-item__error {
    padding-top: 5px;
  }
  .el-radio__label {
    font-size: 12px;
    color: #333;
  }
  .navigation {
    .el-radio {
      margin-left: 10px;
      margin-right: 18px;
    }
  }
  .bottomLine {
    border-bottom: 1px solid #eee;
    padding-bottom: 24px;
  }
  .showClass {
    width: 72px;
    height: 24px;
    color: #999999;
    font-size: 12px;
    text-align: left;
    margin-right: 24px;
    margin-left: 3px;
  }
  .sendType {
    display: flex;
    align-items: center;
    margin-top: 6px;
    .showClass {
      margin-right: 3px;
    }
    .selctColor {
      display: flex;
      align-items: center;
      .el-button {
        margin-left: 6px;
      }
    }
  }
  .bg1,
  .bg2,
  .bg3,
  .bg4,
  .bg5,
  .bg6,
  .bg7,
  .bg8,
  .bg9,
  .bg10,
  .bg11,
  .bg12 {
    display: inline-block;
    width: 22px;
    height: 22px;
    border: 1px solid #dddddd;
    background-color: #fff;
    text-align: center;
    line-height: 26px;
    margin-right: 12px;
    cursor: pointer;
    span {
      display: inline-block;
      width: 16px;
      height: 16px;
    }
  }
  .selsecColor {
    border: 1px solid #007aff !important;
  }
  .bg1 span {
    background-color: #3a3a3a;
  }
  .bg2 span {
    background-color: #db2828;
  }

  .bg3 span {
    background-color: #fb5d06;
  }
  .bg4 span {
    background-color: #c29a6c;
  }
  .bg5 span {
    background-color: #558bd3;
  }
  .bg6 span {
    background-color: #ffffff;
  }

  .bg7 span {
    background-color: #ffffff;
  }
  .bg8 span {
    background-color: #f44877;
  }
  .bg9 span {
    background-color: #ffecee;
  }
  .bg10 span {
    background-color: #fbf5dd;
  }

  .bg11 span {
    background-color: #deebf2;
  }
  .bg12 span {
    background-color: #333;
  }
}
</style>
